import {ColorInfo} from "@/data/ColorInfoProvider";
import CultureManager from "@/CultureManager";

export interface IColorRadioModel {
    id: number;
    nameEng: string;
    nameEst: string;
    isChecked?: boolean;
    imageSrc: string;
    isVisible?: boolean;
    
    getLocalizedName(): string;
}

export class ColorModel implements IColorRadioModel {
    id: number;
    nameEng: string;
    nameEst: string;
    isChecked?: boolean;
    imageSrc: string;
    isVisible?: boolean;
    constructor(id: number, nameEng: string, nameEst: string, imageSrc: string, isVisible = true) {
        this.id = id;
        this.nameEng = nameEng;
        this.nameEst = nameEst;
        this.imageSrc = imageSrc;
        this.isVisible = isVisible;
    }
    
    getLocalizedName(): string {
        return CultureManager.isEstonian ? this.nameEst : this.nameEng;
    }
}

